import React, { useState } from 'react';
import { List, ListItemIcon, ListItemText, ListItemButton, IconButton, Drawer, useMediaQuery, Grid, useTheme } from '@mui/material';
import DashboardIcon from '@mui/icons-material/Dashboard';
import SettingsIcon from '@mui/icons-material/Settings';
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount';
import MenuIcon from '@mui/icons-material/Menu';
import { Link, useLocation } from 'react-router-dom';
import { ADMINISTRATION_ROUTE, CUSTOMERS_ROUTE, OVERVIEW_ROUTE } from '../utils/consts';

const SideBar = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('lg'));
  const location = useLocation();

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const isActiveRoute = (route) => {
    return location.pathname === route;
  };

  const sideBarContent = (
    <List 
      sx={{ 
        '& .MuiListItemButton-root' : {
          padding: '8px 13px'
        },
        paddingLeft: '17px', 
        paddingTop: '0', 
        color: '#54898f' 
      }}>
      <ListItemButton
        component={Link}
        to={OVERVIEW_ROUTE}
        sx={{
          backgroundColor: isActiveRoute(OVERVIEW_ROUTE) ? '#d0f0f2' : 'transparent'
        }}
      >
        <ListItemIcon sx={{ minWidth: '36px' }}>
          <DashboardIcon sx={{ color: '#0f5e65', fontSize: '20px' }} />
        </ListItemIcon>
        <ListItemText
          primary="Overview"
          sx={{
            '& .MuiListItemText-primary': {
              textTransform: 'uppercase',
              fontSize: '12px',
              fontWeight: 'bold'
            }
          }}
        />
      </ListItemButton>
      <ListItemButton
        component={Link}
        to={CUSTOMERS_ROUTE}
        sx={{
          backgroundColor: isActiveRoute(CUSTOMERS_ROUTE) ? '#d0f0f2' : 'transparent'
        }}
      >
        <ListItemIcon sx={{ minWidth: '36px' }}>
          <SupervisorAccountIcon sx={{ color: '#0f5e65', fontSize: '20px' }} />
        </ListItemIcon>
        <ListItemText
          primary="Customers"
          sx={{
            '& .MuiListItemText-primary': {
              textTransform: 'uppercase',
              fontSize: '12px',
              fontWeight: 'bold'
            }
          }}
        />
      </ListItemButton>
      <ListItemButton
        component={Link}
        to={ADMINISTRATION_ROUTE}
        sx={{
          backgroundColor: isActiveRoute(ADMINISTRATION_ROUTE) ? '#d0f0f2' : 'transparent'
        }}
      >
        <ListItemIcon sx={{ minWidth: '36px' }}>
          <SettingsIcon sx={{ color: '#0f5e65', fontSize: '20px' }} />
        </ListItemIcon>
        <ListItemText
          primary="Administration"
          sx={{
            '& .MuiListItemText-primary': {
              textTransform: 'uppercase',
              fontSize: '12px',
              fontWeight: 'bold'
            }
          }}
        />
      </ListItemButton>
    </List>
  );

  return (
    <>
      {isSmallScreen ? (
        <>
          <IconButton onClick={toggleDrawer} 
            sx={{ 
              [theme.breakpoints.down('sm')] : {
                marginLeft: '2px!important', 
              },
              [theme.breakpoints.down('md')] : {
                marginLeft: '4px', 
              },
              marginLeft: '7px', 
              position: "fixed" 
            }}>
            <MenuIcon />
          </IconButton>
          <Drawer 
            anchor="left" 
            open={drawerOpen} 
            onClick={toggleDrawer} 
            sx={{
              
              '& .MuiDrawer-paper' : {
                [theme.breakpoints.down('sm')] : {
                  top: '100px',
                },
                top: '135px',
                border: 'none'
              },
              '& .MuiList-root' : {
                paddingTop: '8px',
                paddingLeft: '0'
              }
                
            }}>
            {sideBarContent}
          </Drawer>
        </>
      ) : (
        <Grid item position={'fixed'}>
          {sideBarContent}
        </Grid>
      )}
    </>
  );
};

export default SideBar;
