import React, { useState } from 'react';
import { 
  Card, 
  CardContent, 
  Button, 
  CardHeader, 
  List, 
  ListItemIcon, 
  ListItemText, 
  ListItem, 
  Box, 
  Typography, 
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions, 
  Stack, 
  TextField, 
  IconButton,
  useTheme
} from '@mui/material';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';
import PlaceIcon from '@mui/icons-material/Place';
import LocationCityIcon from '@mui/icons-material/LocationCity';
import ModeEditIcon from '@mui/icons-material/ModeEdit';
import CloseIcon from '@mui/icons-material/Close';

const CustomerDetails = () => {

  const theme = useTheme();

  const [open, setOpen] = useState(false);

  const [customerInfo, setCustomerInfo] = useState({
    name: "Oleksandr",
    phoneNumber: "+00000",
    email: "test@gmail.com",
    street: "Mazepy",
    city: "Ivano-Frankivsk"
  });

  const [name, setName] = useState(customerInfo.name);
  const [phoneNumber, setPhoneNumber] = useState(customerInfo.phoneNumber);
  const [email, setEmail] = useState(customerInfo.email);
  const [street, setStreet] = useState(customerInfo.street);
  const [city, setCity] = useState(customerInfo.city);
  const [errors, setErrors] = useState({});

  const openPopup = () => {
    setName(customerInfo.name);
    setPhoneNumber(customerInfo.phoneNumber);
    setEmail(customerInfo.email);
    setStreet(customerInfo.street);
    setCity(customerInfo.city);
    setErrors({});
    setOpen(true);
  }

  const closePopup = () => {
    setOpen(false);
  }

  const validateFields = () => {
    const newErrors = {};
    if (!name) newErrors.name = 'Customer name is required';
    if (!phoneNumber) newErrors.phoneNumber = 'Phone number is required';
    if (!email) {
      newErrors.email = 'E-mail is required';
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/i.test(email)) {
      newErrors.email = 'E-mail is not valid';
    }
    if (!street) newErrors.street = 'Street is required';
    if (!city) newErrors.city = 'City is required';
    return newErrors;
  };

  const handleSave = () => {
    const validationErrors = validateFields();
    if (Object.keys(validationErrors).length === 0) {
      setCustomerInfo({
        name,
        phoneNumber,
        email,
        street,
        city
      });
      closePopup();
    } else {
      setErrors(validationErrors);
    }
  }

  return (
    <>
      <Box>
        <Box 
          sx={{
            padding: '9px 12px',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}>
          <Typography sx={{
            color: '#87898b',
            fontSize: '15px'
          }}>
            Customer
          </Typography>
          <Button startIcon={<ModeEditIcon sx={{color: '#000000', maxWidth: '16px'}}/>} sx={{
            border: 'solid 1px #c6c2c2',
            height: '30px',
            width: '75px!important',
            color: '#000000',
            fontSize: '12px',
            fontWeight: 'bold'
          }}
          onClick={openPopup}
          >
            edit
          </Button>
        </Box>
        <Card>
          <CardHeader avatar={
            <AccountCircleIcon 
              sx={{
                  color: '#0f5e65',
                  fontSize: '32px'
              }}/>
            }
            title={customerInfo.name}
            sx={{
                paddingBottom: '0px',
                '& .MuiCardHeader-title': {
                fontSize: '22px',
                }
            }}
          />
          <CardContent>
            <List>
              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <PhoneIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={customerInfo.phoneNumber}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <EmailIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={customerInfo.email}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <PlaceIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={customerInfo.street}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <LocationCityIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={customerInfo.city} />
              </ListItem>
              
            </List>
          </CardContent>
        </Card>
      </Box>  

      <Dialog open={open} maxWidth='xs' fullWidth={true}>
        <DialogActions sx={{
          display: 'flex',
          justifyContent: 'space-between'
        }}>
          <DialogTitle textAlign={'left'} 
          sx={{
            [theme.breakpoints.down('sm')] : {
              padding: '10px 15px',
              fontSize: '16px'
            },
          }}>Edit Customer Details</DialogTitle>
          <IconButton onClick={closePopup} sx={{ padding: 0 }}>
            <CloseIcon sx={{
              [theme.breakpoints.down('sm')] : {
                fontSize: '28px!important'
              },
              fontSize: '35px!important',
              color: 'red',
              transition: 'transform 0.3s',
              '&:hover': {
                transform: 'rotate(180deg)',
              },
            }} />
          </IconButton>
        </DialogActions>
        <DialogContent sx={{
          [theme.breakpoints.down('sm')] : {
            padding: '9px 13px',
            '& .MuiStack-root' : {
              margin: '5px'
            }
          },
        }}>
          <Stack spacing={2} margin={2} sx={{
            '& .MuiOutlinedInput-input' : {
              padding: '13px 12px'
            }
          }}>
            <TextField 
              variant='outlined' 
              label='Customer name'
              value={name}
              onChange={(e) => setName(e.target.value)}
              error={!!errors.name}
              helperText={errors.name}
            />
            <TextField 
              variant='outlined' 
              label='Phone number'
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              error={!!errors.phoneNumber}
              helperText={errors.phoneNumber}
            />
            <TextField 
              variant='outlined' 
              label='E-mail'
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              error={!!errors.email}
              helperText={errors.email}
            />
            <TextField 
              variant='outlined' 
              label='Street'
              value={street}
              onChange={(e) => setStreet(e.target.value)}
              error={!!errors.street}
              helperText={errors.street}
            />
            <TextField 
              variant='outlined' 
              label='City'
              value={city}
              onChange={(e) => setCity(e.target.value)}
              error={!!errors.city}
              helperText={errors.city}
            />
          </Stack>
        </DialogContent>
        <DialogActions sx={{justifyContent: 'center'}}>
          <Button onClick={handleSave} variant='contained' 
          sx={{
            [theme.breakpoints.down('sm')] : {
              width: '60px',
              fontSize: '10px',
              height: '26px'
            },
            backgroundColor: '#0f5e65',
              '&:hover' : {
                backgroundColor: '#0d4c55'
          }}}>Confirm</Button>
        </DialogActions>
      </Dialog>
    </>
  )
}

export default CustomerDetails;
