import React, { useState } from 'react';
import { 
  Card, 
  CardContent, 
  Button, 
  CardHeader, 
  List, 
  ListItemIcon, 
  ListItemText, 
  ListItem, 
  Box, 
  Typography, 
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions, 
  Stack, 
  TextField,
  IconButton,
  useTheme
} from '@mui/material';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import ModeEditIcon from '@mui/icons-material/ModeEdit';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import ExtensionIcon from '@mui/icons-material/Extension';
import LocalGasStationIcon from '@mui/icons-material/LocalGasStation';
import PowerIcon from '@mui/icons-material/Power';
import CloseIcon from '@mui/icons-material/Close';
import dayjs from 'dayjs';

const VehicleDetails = () => {
  const theme = useTheme();
  
  const [open, setOpen] = useState(false);

  const [vehicleInfo, setVehicleInfo] = useState({
    licensePlate: 'AT0000AK',
    vin: 'AMLMEAF931LKS0000',
    dateOfIssue: dayjs('2024-02-24'),
    model: 'V60',
    fuelType: 'Gasoline',
    batteryCapacity: '140'
  });

  const [licensePlate, setLicensePlate] = useState(vehicleInfo.licensePlate);
  const [vin, setVin] = useState(vehicleInfo.vin);
  const [dateOfIssue, setDateOfIssue] = useState(vehicleInfo.dateOfIssue);
  const [model, setModel] = useState(vehicleInfo.model);
  const [fuelType, setFuelType] = useState(vehicleInfo.fuelType);
  const [batteryCapacity, setBatteryCapacity] = useState(vehicleInfo.batteryCapacity);
  const [errors, setErrors] = useState({});

  const openPopup = () => {
    setLicensePlate(vehicleInfo.licensePlate);
    setVin(vehicleInfo.vin);
    setDateOfIssue(vehicleInfo.dateOfIssue);
    setModel(vehicleInfo.model);
    setFuelType(vehicleInfo.fuelType);
    setBatteryCapacity(vehicleInfo.batteryCapacity);
    setOpen(true);
  }

  const closePopup = () => {
    setOpen(false);
    setErrors({});
  }

  const validateFields = () => {
    const newErrors = {};
    if (!licensePlate) newErrors.licensePlate = 'License plate is required';
    if (!vin) {
      newErrors.vin = 'VIN is required';
    } else if (vin.length !== 17) {
      newErrors.vin = 'VIN must be exactly 17 characters';
    }
    if (!dateOfIssue) newErrors.dateOfIssue = 'Date of issue is required';
    if (!model) newErrors.model = 'Model name is required';
    if (!fuelType && !batteryCapacity) {
      newErrors.fuelType = 'Either fuel type or battery capacity is required';
      newErrors.batteryCapacity = 'Either fuel type or battery capacity is required';
    }
    return newErrors;
  };

  const handleSave = () => {
    const validationErrors = validateFields();
    if (Object.keys(validationErrors).length === 0) {
      setVehicleInfo({
        licensePlate,
        vin,
        dateOfIssue,
        model,
        fuelType,
        batteryCapacity
      });
      closePopup();
    } else {
      setErrors(validationErrors);
    }
  }

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <Box>
        <Box 
          sx={{
            padding: '9px 16px',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}>
          <Typography sx={{
            color: '#87898b',
            fontSize: '15px'
          }}>
            Vehicle
          </Typography>
          <Button startIcon={<ModeEditIcon sx={{color: '#000000', maxWidth: '16px'}}/>} sx={{
            border: 'solid 1px #c6c2c2',
            height: '30px',
            width: '75px!important',
            color: '#000000',
            fontSize: '11px',
            fontWeight: 'bold'
          }}
          onClick={openPopup}
          >
            edit
          </Button>
        </Box>
        <Card>
          <CardHeader avatar={
            <DirectionsCarIcon 
              sx={{
                  color: '#0f5e65',
                  fontSize: '32px'
              }}/>
            }
            title={vehicleInfo.licensePlate}
            sx={{
                paddingBottom: '0px',
                '& .MuiCardHeader-title': {
                fontSize: '22px',
                textTransform: 'uppercase'
                }
            }}
          />
          <CardContent>
            <List>
              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <ModeEditIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={vehicleInfo.vin} sx={{textTransform: 'uppercase'}}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <CalendarTodayIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={dayjs(vehicleInfo.dateOfIssue).format('YYYY-MM-DD')}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <ExtensionIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={vehicleInfo.model}/>
              </ListItem>

              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <LocalGasStationIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={vehicleInfo.fuelType}/>
              </ListItem>
              
              <ListItem sx={{paddingLeft: '0',
                padding: '5px 0', '& .MuiTypography-root' : {
                  fontSize: '11.5px'
                }}}>
                <ListItemIcon sx={{ minWidth: '36px' }}>
                  <PowerIcon sx={{ color: '#0f5e65', fontSize: '17px' }}/>
                </ListItemIcon>
                <ListItemText primary={vehicleInfo.batteryCapacity}/>
              </ListItem>

            </List>
          </CardContent>
        </Card>
      </Box>

      <Dialog open={open} maxWidth='xs' fullWidth={true}> 
        <DialogActions sx={{
          display: 'flex',
          justifyContent: 'space-between'
        }}>
          <DialogTitle textAlign={'left'} 
          sx={{
            [theme.breakpoints.down('sm')] : {
              padding: '10px 15px',
              fontSize: '16px'
            },
          }}>Edit Vehicle Details</DialogTitle>
          <IconButton onClick={closePopup} sx={{ padding: 0 }}>
            <CloseIcon sx={{
              [theme.breakpoints.down('sm')] : {
                fontSize: '28px!important'
              },
              fontSize: '35px!important',
              color: 'red',
              transition: 'transform 0.3s',
              '&:hover': {
                transform: 'rotate(180deg)',
              },
            }} />
          </IconButton>
        </DialogActions>
        <DialogContent sx={{
          [theme.breakpoints.down('sm')] : {
            padding: '9px 13px',
            '& .MuiStack-root' : {
              margin: '5px'
            }
          },
        }}>
          <Stack spacing={2} margin={2} sx={{
            '& .MuiOutlinedInput-input' : {
              padding: '13px 12px'
            }
          }}>
            <TextField 
              variant='outlined' 
              label='License plate' 
              value={licensePlate} 
              onChange={(e) => setLicensePlate(e.target.value.toUpperCase())}
              error={!!errors.licensePlate}
              helperText={errors.licensePlate}
            />
            <TextField 
              variant='outlined' 
              label='VIN' 
              value={vin} 
              onChange={(e) => setVin(e.target.value.toUpperCase())}
              error={!!errors.vin}
              helperText={errors.vin}
            />

            <DatePicker
              label="Date of issue"
              value={dateOfIssue}
              onChange={(newValue) => setDateOfIssue(newValue)}
              renderInput={(params) => 
                <TextField {...params}
                  error={!!errors.dateOfIssue}
                  helperText={errors.dateOfIssue}
                />}
            />
            <TextField 
              variant='outlined' 
              label='Model name' 
              value={model} 
              onChange={(e) => setModel(e.target.value)}
              error={!!errors.model}
              helperText={errors.model}
            />
            <TextField 
              variant='outlined' 
              label='Fuel type' 
              value={fuelType} 
              onChange={(e) => setFuelType(e.target.value)}
              error={!!errors.fuelType}
              helperText={errors.fuelType}
            />
            <TextField 
              variant='outlined' 
              label='Battery capacity(kW)' 
              value={batteryCapacity} 
              onChange={(e) => setBatteryCapacity(e.target.value)}
              error={!!errors.batteryCapacity}
              helperText={errors.batteryCapacity}
            />
          </Stack>
        </DialogContent>
        <DialogActions sx={{justifyContent: 'center'}}>
          <Button onClick={handleSave} variant='contained'
          sx={{
            [theme.breakpoints.down('sm')] : {
              width: '60px',
              fontSize: '10px',
              height: '26px'
            },
            backgroundColor: '#0f5e65',
            '&:hover' : {
              backgroundColor: '#0d4c55'
        }}}>Confirm</Button>
        </DialogActions>
      </Dialog>
    </LocalizationProvider>
  )
}

export default VehicleDetails;
