import React, { useState, useEffect } from 'react';
import { 
  AppBar, 
  Tabs, 
  Tab, 
  Button, 
  Grid, 
  Typography, 
  useTheme,
  useMediaQuery
} from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate, useLocation } from 'react-router-dom';
import ContractState from './ContractState';

const Header = () => {
  const [contractState, setContractState] = useState('Active');
  const navigate = useNavigate();
  const location = useLocation();
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleBuyNow = () => {
    setContractState('ActivePaid');
  };

  const handleCloseOffer = () => {
    setContractState('Closed');
  };

  const handleTabChange = (event, newValue) => {
    navigate(newValue);
  };

  useEffect(() => {
    if (location.pathname === '/') {
      navigate('/practiceTask/general');
    }
  }, [location, navigate]);

  const determineTabValue = () => {
    if (location.pathname.startsWith('/practiceTask/general')) {
      return '/practiceTask/general';
    } else if (location.pathname.startsWith('/practiceTask/statistics')) {
      return '/practiceTask/statistics';
    }
    return false;
  };

  return (
    <AppBar position='fixed' 
      sx={{ 
        [theme.breakpoints.down('sm')] : {
          height: '100px',
        },
        backgroundColor: '#54898f', 
        height: '135px', 
        display: 'flex', 
        flexDirection: 'column', 
        justifyContent: 'space-between',
        paddingTop: '25px', paddingRight: '25px',
        boxShadow: 'none'
      }}
    >
      <Grid container>
        <Grid item xs={2} sm={1} md={1} lg={2}></Grid>
        <Grid item xs={10} sm={11} md={11} lg={10} sx={{
          display: 'flex',
          alignItems: 'center'
        }}>
          <Typography 
            sx={{
              [theme.breakpoints.down('sm')] : {
                fontSize: '17px',
              },
              fontSize: '27px',
              fontWeight: 'light'
            }}
          >
            New vehicle contract
          </Typography>
          <ContractState contractState={contractState} />
        </Grid>
      </Grid>
      <Grid container sx={{ alignItems: 'flex-end' }}>
        <Grid item xs={2} sm={1} md={1} lg={2}></Grid>
        <Grid item xs={10} sm={11} md={11} lg={10}>
          <Grid container justifyContent="space-between" alignItems="flex-start">
            <Grid item sx={{
              [theme.breakpoints.down('sm')] : {
                height: '30px'
              },
              height: '38px'
            }}>
              <Tabs
                value={determineTabValue()}
                onChange={handleTabChange}
                textColor="inherit"
                indicatorColor="none"
                sx={{
                  [theme.breakpoints.down('sm')] : {
                    '& .MuiTabs-scroller' : {
                      height: '30px!important'
                    },
                    '& .MuiTab-root': {
                      padding: '5px 10px!important',
                      minWidth: '50px!important',
                      fontSize: '9px!important',
                      height: '30px!important'
                    }
                  },
                  minHeight: '30px!important',
                  '& .MuiTabs-scroller' : {
                    height: '38px',
                    minHeight: '30px'
                  },              
                  '& .MuiTab-root': {
                    boxSizing: 'border-box',
                    padding: '9px 25px',
                    borderTopLeftRadius: '11px!important',
                    borderTopRightRadius: '11px!important',
                    borderBottomLeftRadius: '0px!important',
                    borderBottomRightRadius: '0px!important',
                    backgroundColor: '#0f5e65',
                    borderColor: '#0f5e65',
                    borderBottom: 'none',
                    fontWeight: 'bold',
                    fontSize: '11px',
                    minHeight: '30px',
                    height: '38px',
                    '&:hover': {
                    backgroundColor: '#0d4c55',
                    },
                  },
                  '& .Mui-selected': {
                    backgroundColor: '#f7f7f7',
                    color: '#000000',
                    border: 'solid 2px #000000',
                    borderBottom: 'none',
                    '&:hover': {
                    backgroundColor: '#f7f7f7'
                    },
                  },
                }}
              >
                <Tab label="General" value="/practiceTask/general" />
                <Tab label="Statistics" value="/practiceTask/statistics" />
              </Tabs>
            </Grid>
            <Grid item>
              <Button 
                startIcon={<ShoppingCartIcon 
                  sx={{
                    [theme.breakpoints.down('sm')] : {
                      marginRight: '-12px',
                    },
                  }}/>} 
                sx={{
                  [theme.breakpoints.down('sm')] : {
                    minWidth: '30px!important',
                    width: '30px!important',
                    padding: '3px!important',
                    marginRight: '5px!important'
                  },
                  [theme.breakpoints.down('md')] : {
                    padding: '5px 10px',
                    fontSize: '10px',
                    marginRight: '10px'
                  },
                  boxSizing: 'border-box',
                  backgroundColor: '#0f5e65',
                  color: '#ffffff',
                  padding: '6px 20px',
                  fontSize: '12px',
                  marginRight: '25px',
                  '&:hover': {
                    backgroundColor: '#0d4c55',
                  },
                }}
                onClick={handleBuyNow}
              >
                {isSmallScreen ? '' : 'Buy Now'}
              </Button>
              <Button 
                startIcon={<CloseIcon 
                  sx={{
                    [theme.breakpoints.down('sm')] : {
                      marginRight: '-12px'
                    },
                  }}
                />}
                sx={{
                  [theme.breakpoints.down('sm')] : {
                    minWidth: '30px!important',
                    width: '30px!important',
                    padding: '3px!important',
                  },
                  [theme.breakpoints.down('md')] : {
                    padding: '5px 10px',
                    fontSize: '10px',
                  },
                  backgroundColor: '#a13f40',
                  color: '#ffffff',
                  fontSize: '12px',
                  padding: '6px 20px',
                  '&:hover': {
                    backgroundColor: '#8e3434',
                  },
                }}
                onClick={handleCloseOffer}
              >
                {isSmallScreen ? '' : 'Close Offer'}
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </AppBar>
  );
}

export default Header;
