import React from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Box,
  Typography
} from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

const createData = (date, comment) => {
  return { date, comment };
};

const rows = [
  createData('02.02.2024 14:22', 'Buy Now'),
  createData('01.02.2024 03:22', 'Created')
];

const Events = () => {
  return (
    <Box>
        <Box 
            sx={{
            padding: '9px 16px',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            }}>
            <Typography sx={{
                color: '#87898b',
                fontSize: '15px'
            }}>
                Events
            </Typography>
        </Box>
        <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650,
                '& .MuiTableCell-root' : {
                    fontSize: '12px',
                    padding: '10px 16px'
                }
             }} aria-label="simple table">
            <TableHead>
                <TableRow>
                <TableCell>
                    <Button 
                        endIcon={<ArrowDropDownIcon sx={{fontSize: '20px!important'}}/>}
                        sx={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            paddingTop: '2px',
                            paddingBottom: '2px',
                            paddingLeft: '0',
                            color: '#000000',
                            fontWeight: 'bold',
                            textTransform: 'none', 
                            fontSize: '12.5px',
                            minWidth: '50px',
                            '& .MuiButton-endIcon' : {
                                ml: '0'
                            },

                        }}
                    >
                        Date
                    </Button></TableCell>
                <TableCell align="left" sx={{color: '#87898b'}}>Type / Comment</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {rows.map((row, index) => (
                <TableRow
                    key={index}
                    sx={{ '&:nth-of-type(odd)': {
                        backgroundColor: '#f4f4f4',
                        },'&:last-child td, &:last-child th': 
                        { border: 0 }
                    }}
                >
                    <TableCell component="th" scope="row">
                    {row.date}
                    </TableCell>
                    <TableCell align="left">{row.comment}</TableCell>
                </TableRow>
                ))}
            </TableBody>
            </Table>
        </TableContainer>
    </Box>
  );
};

export default Events;