import React from "react";
import { Route, Routes, Navigate } from 'react-router-dom';
import General from './components/General';
import Statistics from './components/Statistics';

const AppRouter = () => {
    return (
        <Routes>
            <Route path="/" element={<Navigate to="/practiceTask/general" />} />
            <Route path="/practiceTask/general/*" element={<General />} />
            <Route path="/practiceTask/statistics" element={<Statistics />} />
        </Routes>
    );
};

export default AppRouter;
