import React from 'react';
import { 
  Grid, 
  Card, 
  CardContent, 
  CardHeader, 
  ListItem, 
  List, 
  Box, 
  Typography, 
  useTheme, 
  useMediaQuery 
} from '@mui/material';
import FilterNoneIcon from '@mui/icons-material/FilterNone';

const ContractDetails = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box>
      <Box 
        sx={{
          padding: '9px 16px',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}>
        <Typography sx={{
            color: '#87898b',
            fontSize: '15px'
        }}>
            Contract details
        </Typography>
      </Box>
  
      <Card>
        <CardHeader 
          avatar={
            <FilterNoneIcon 
              sx={{
                  color: '#0f5e65',
                  fontSize: '32px'
              }}
            />
          }
          title="New vehicle contract"
          sx={{
              paddingBottom: '0px',
              '& .MuiCardHeader-title': {
                fontSize: '22px',
              }
          }}
        />
        <CardContent>
          <Grid 
            container 
            spacing={2} 
            direction={isSmallScreen ? 'column' : 'row'}
            sx={{
              flexWrap: isSmallScreen ? 'wrap' : 'nowrap'
            }}
          >
            <Grid 
              item 
              xs={12} 
              md={6} 
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                paddingRight: isSmallScreen ? '0' : '35px'
              }}
            >
              <List sx={{ color: '#87898b' }}>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>ID #:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Duration / Distance:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Start Date:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>End Date:</ListItem>
              </List>
              
              <List>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>00001</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>36 months / 140,000 km</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>01.02.2024</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>01.02.2027</ListItem>
              </List>
            </Grid>
            <Grid 
              item 
              xs={12} 
              md={6} 
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                paddingLeft: isSmallScreen ? '0' : '35px',
                paddingRight: isSmallScreen ? '0' : '17px'
              }}
            >
              <List sx={{ color: '#87898b' }}>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Total Price:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Customer price/mth.:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Odometer at expiration:</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11px'}}>Start Mileage:</ListItem>
              </List>
              
              <List>
                <ListItem sx={{padding: '0', fontSize: '11.5px'}}>3.555,00 $</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11.5px'}}>98,75 $</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11.5px'}}>140.001 km</ListItem>
                <ListItem sx={{padding: '0', fontSize: '11.5px'}}>1 km</ListItem>
              </List>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Box>
  );
};

export default ContractDetails;
