import React from 'react';
import { Grid, Box } from '@mui/material';
import { Route, Routes, useLocation} from 'react-router-dom';
import SideBar from './SideBar';
import Overview from '../pages/Overview';
import Customers from '../pages/Customers';
import Administration from '../pages/Administration';

const General = () => {
  const location = useLocation();
  return (
    <Grid container >
      <Grid item xs={2} sm={1} md={1} lg={2}>
        <SideBar />
      </Grid>
      <Grid item xs={10} sm={11} md={11} lg={10}>
      {location.pathname === "/practiceTask/general" ? (
          <Box sx={{textAlign: 'center'}}>
            <h3>Welcome to the management system!</h3>
            <div>Please select one of the menu items on the left to get started!</div>
          </Box>

        ) : (
        <Routes>
          <Route path="/overview" element={<Overview />} />
          <Route path="/customers" element={<Customers />} />
          <Route path="/administration" element={<Administration />} />
        </Routes>
      )}
      </Grid>
    </Grid>
  );
};

export default General;
